.card {
    height: 208px;
    background-color: white;
    width: 576px;
    border-radius: 12px;
    box-shadow: 1px 3px 12px #aaa;
    margin-bottom: 30px;
    cursor: pointer;
    transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 0.4s;
}

.card:hover{
    box-shadow: 1px 3px 20px #999;
}

.ca-p-a {
    height: 145.6px;
    display: flex;
    justify-content: space-between;
    border-radius: 12px 12px 0px 0px;
}

.cap-a {
    height: 72.8px;
    width: 40vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 12px 0px 0px 0px;
}

.cap-1 {
    width: 44vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.d1 {
    font-size: 19px;
    font-weight: bold;
}

.d2 {
    font-size: 19px;
    color: grey;
}

.dm-2 {
    font-size: 19px;
}

.cap-b {
    height: 72.8px;
    width: 36.5vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.d1 {
    font-size: 19px;
    font-weight: bold;
}

.dm-2 {
    font-size: 19px;
}

.cap-2 {
    font-size: 20px;
    padding: 23px;
    font-weight: bold;
    border-radius: 0px 12px 0px 0px;
}

.ca-p-b {
    height: 62.4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0px 0px 12px 12px;
}

.ic-11 {
    display: flex;
    align-items: center;
}

.ic-1 {
    font-size: 41px;
    padding: 12px;
}

.ic-3 {
    font-size: 23px;
    padding: 10px;
}


