.top_bar{
    display: flex;
    justify-content: space-between;

}

.logo{
    width: 120px;
    margin: 20px;
    cursor: pointer;
}

.searchLogo{
    width: 30px;
    height: 30px;
    margin: auto;
    margin-right: 10px;
    filter: grayscale(100%);
    transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
}

.search:hover .searchLogo{
    filter: grayscale(0%);
}

.user{
    margin: 10px 50px;
    padding: 5px;
    display: flex;
}

.profile_pic{
    width: 40px;
    height: 40px;
    margin: 10px;
}

.user_name{
    margin: auto;
    padding: 10px;
    cursor: pointer;
    user-select: none;
    color: #333;
    transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
}

.sign_out{
    position: absolute;
    width: fit-content;
    background-color: #fff;
    box-shadow: 0 0 10px 2px #e4b159;
    border-radius: 10px;
    padding: 1px 25px;
    right: 0;
    margin-right: 100px;
    user-select: none;
    cursor: pointer;
    border: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    z-index: 100;
}

.sign_out:hover{
    color: rgb(192, 47, 47);
}

.login_text{
    text-decoration: none;
    color: #666;
    transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
}

.login_text:hover{
    color: #d3962c;
}

.search{
    display: flex;
    width: fit-content;
    
}

.pub_ride, .search{
    text-decoration: none;
    margin: auto;
    margin-right: 20px;
    color: #666;
    font-weight: bold;
    transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
}

.pub_ride:hover, .search:hover, .user_name:hover{
    color: #d3962c;
}

@media only screen and (max-width: 600px) {
    .user_name{
        display: none;
    
    }
    .pub_ride{
        display: none;
    }
    .logo{
        width: 90px;
        height: 40px;
    }
  }